.container {
    padding: 2rem;
    max-width: 1400px;
    margin: 0 auto;
}

/* Checkout title */
.container h1 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 1.5rem;
}

/* Checkout message */
.container p {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 1rem;
}

/* Back to Cart button */
.back-to-cart {
    padding: 0.8rem 1.5rem;
    border: none;
    border-radius: 4px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    background-color: #007bff;
    color: white;
    margin-top: 1rem;
}

.back-to-cart:hover {
    background-color: #0056b3;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .container {
        padding: 1rem;
    }

    .container h1 {
        font-size: 1.75rem;
    }
}